footer #banner{
    background: #3A3053;
}

footer #banner div{
    width: 100%;
    height: 30vh;
    text-align: center;
    background: url(../images/bg-boost-desktop.svg) no-repeat;
    background-size: cover;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

footer #banner div h1{
    color: white;
    font-size: 4rem;
    font-weight: 800;
}

footer #banner div .btnGetStarted{
    margin-top: 2rem;
}

footer #footerWrapper{
    height: 40vh;
    background: #232027;
    position: relative;
}
footer #footerWrapper #footer{
    width: 100%;
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    display: flex;
    justify-content: space-evenly;
}

footer #footerWrapper #footer #links{
    width: 30%;
    display: flex;
    justify-content: space-between;
}

footer #footerWrapper #footer #links section{
    font-size: 2.5rem;
}

footer #footerWrapper #footer #links section h4{
    color: white;
    margin-bottom: 2rem;
}

footer #footerWrapper #footer #links section a{
    display: block;
    color: #9B999E;
    margin-bottom: 1rem;
}

footer #footerWrapper #footer #socialMedia *{
    margin: 1rem;
}

footer #footerWrapper #footer #socialMedia *:hover{
    cursor: pointer;
    transition: .2s linear;
    fill: #4B406B;
}











@media screen and (max-width:480px) {
    footer #banner div{
        width: 100%;
        height: 40vh;
        background: url(../images/bg-boost-mobile.svg) no-repeat;
        background-size: cover;
    }
    
    footer #banner div h1{
        font-size: 2.5rem;
    }
    
    footer #footerWrapper{
        height: 100vh;
        background: #232027;
        position: static;
    }

    footer #footerWrapper #footer{
        height: 100vh;
        position: static;
        top: auto;
        transform: none;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
    }
    
    footer #footerWrapper #footer #links{
        display: block;
    }

    footer #footerWrapper #footer #links section{
        font-size: 2rem;
        text-align: center;
        margin: 2rem 0;
    }
}



@media screen and (min-width:481px) and (max-width:700px) {
    footer #banner div{
        width: 100%;
        height: 40vh;
        background: url(../images/bg-boost-mobile.svg) no-repeat;
        background-size: cover;
    }
    
    footer #banner div h1{
        font-size: 3.5rem;
    }
    
    footer #footerWrapper{
        height: 100vh;
        background: #232027;
        position: static;
    }

    footer #footerWrapper #footer{
        height: 100vh;
        position: static;
        top: auto;
        transform: none;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
    }
    
    footer #footerWrapper #footer #links{
        display: block;
    }

    footer #footerWrapper #footer #links section{
        font-size: 2.5rem;
        text-align: center;
        margin: 2rem 0;
    }
}