#input{
    padding: 2% 10%;
    background: #F0F1F7;
}

.loader{
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
}

#input #wrapper{
    width: 100%;
    height: 20vh;
    background: #3A3053;
    border-radius: 1rem;
    position: relative;
}

#input #wrapper div{
    width: 100%;
    height: 100%;
    background: url(../images/bg-shorten-desktop.svg) no-repeat;
    background-size: cover;
    border-radius: 1rem;
    display: flex;
    justify-content: space-between;
    align-items: center;
}

#input #wrapper div input {
    width: 70%;
    font-size: 1.8rem;
    font-weight: 500;
    border-radius: 1rem;
    padding: 2rem;
    margin-left: 7rem;
    outline: none;
}

.button{
    width: 15%;
    border: none;
    background: #3A3053;
    color: white;
    border-radius: 1rem;
    padding: 2rem;
    margin-right: 7rem;
    font-size: 1.8rem;
    font-weight: 600;
}
.button:hover{
    transition: .3s linear;
    background: #9BE3E2;
    color: #3A3053;
    cursor: pointer;
}

#input #wrapper #error{
    font-size: 1.5rem;
    font-weight: 600;
    margin-left: 7rem;
    font-style: italic;
    color: #CF5E79;
    position: absolute;
    transform: translateY(-5vh);
}

.result{
    width: 100%;
    font-size: 2rem;
    padding: 2rem;
    margin: 2rem auto;
    background: white;
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 1rem;
}

.result a:last-of-type{
    margin-left: auto;
    color: #3A3053;
    font-weight: 700;
}

.result .button{
    width: auto;
    border: none;
    background: #3A3053;
    color: white;
    border-radius: 1rem;
    padding: 1rem 3rem;
    margin: 0 0 0 3rem;
    font-weight: 600;
}

.result .button:hover{
    transition: .3s linear;
    background: #9BE3E2;
    color: #3A3053;
    cursor: pointer;
}












@media screen and (max-width:480px) {
    #input{
        padding: 2% 10%;
        background: #F0F1F7;
    }
    
    .loader{
        width: 100%;
        display: flex;
        justify-content: center;
        align-items: center;
    }
    
    #input #wrapper{
        width: 100%;
        height: 20vh;
        background: #3A3053;
        border-radius: 1rem;
        position: relative;
    }
    
    #input #wrapper div{
        width: 100%;
        height: 100%;
        background: url(../images/bg-shorten-mobile.svg) no-repeat;
        background-size: cover;
        border-radius: 1rem;
        display: flex;
        flex-direction: column;
        justify-content: space-evenly;
        align-items: center;
    }
    
    #input #wrapper div input {
        width: 90%;
        font-size: 1.3rem;
        padding: .7rem;
        margin: 0;
    }
    
    .button{
        width: 90%;
        padding: .7rem;
        margin: 0;
        font-size: 1.3rem;
    }
    
    #input #wrapper #error{
        font-size: 1rem;
        margin-left: 1.5rem;
        transform: translateY(-10.5vh);
    }
    
    .result{
        padding: 1rem;
        display: flex;
        flex-direction: column;
        justify-content: space-evenly;
        align-items: center;
    }
    
    .result h1{
        margin: 1rem 0;
    }
    
    .result a:last-of-type{
        margin: 0;
        color: #3A3053;
        font-weight: 700;
        margin: 1rem 0;
    }
    
    .result .button{
        width: 90%;
        margin: 0;
    }
}



