@import url('https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap');

*{
    padding: 0;
    margin: 0;
    box-sizing: border-box;
    list-style: none;
    font-size: 62.5%;
    text-decoration: none;
    font-family: 'Poppins', sans-serif;
}



@media screen and (min-width:701px) and (max-width:1024px){
    body nav ul #navbar>li{
        font-size: 2rem;
        padding: 1rem 2rem;
    }

    body #home{
        height: 50vh;
    }

    body #home div div h1{
        font-size: 5rem;
    }

    body #home div div h5{
        font-size: 1.2rem;
    }

    body .btnGetStarted{
        font-size: 1.5rem;
        padding: 1rem 2.5rem;
    }

    body #input #wrapper{
        height: 15vh;
    }

    body #input #wrapper div input {
        font-size: 1.5rem;
        padding: 1rem;
        margin-left: 3rem;
    }
    
    #input #wrapper div button{
        padding: 1rem;
        margin-right: 3rem;
        font-size: 1.5rem;
    }

    body #input #wrapper #error{
        font-size: 1.2rem;
        margin-left: 3rem;
        transform: translateY(-4vh);
    }

    body #statistics #title h1{
        font-size: 3.5rem;
    }
    
    body #statistics #title h5{
        font-size: 1.5rem;
    }

    body #statistics #tiles div h3{
        font-size: 1.7rem;
    }
    
    body #statistics #tiles div p{
        font-size: 1.1rem;
    }

    body .img-wrapper{
        width: 6rem;
        height: 6rem;
        top: -3rem;
    }

    #statistics #tiles div img{
        width: 3rem;
        height: 3rem;
    }

    body footer #banner div{
        height: 25vh;
    }
    
    body footer #banner div h1{
        font-size: 3.2rem;
    }

    body footer #footerWrapper{
        height: 30vh;
    }

    body footer #footerWrapper #footer #links section{
        margin: 0 1rem;
        font-size: 2rem;
    }

    body footer #footerWrapper #footer #socialMedia *{
        margin: .7rem;
    }
}


@media screen and (min-width:1024px) and (max-width:1200px){
    body #home div div h1{
        color: #3F3D56;
        font-size: 6rem;
        font-weight: 700;
        line-height: 110%;
    }
    body #home div div h5{
        width: 80%;
        font-size: 1.5rem;
        font-weight: 500;
        color: #2BD1D1;
    }

    body .btnGetStarted{
        font-size: 1.8rem;
        padding: 1.2rem 3rem;
    }

    body #input #wrapper div input {
        font-size: 1.5rem;
        padding: 1.8rem;
        margin-left: 5rem;
    }
    
    #input #wrapper div button{
        padding: 1.8rem;
        margin-right: 5rem;
        font-size: 1.5rem;
    }

    body #input #wrapper #error{
        margin-left: 5rem;
    }

    body #statistics #title h1{
        font-size: 4rem;
    }
    
    body #statistics #title h5{
        font-size: 1.8rem;
    }

    body #statistics #tiles>div{
        width: 32%;
        padding: 0 2.5rem 2.5rem 2.5rem;
    }

    body #statistics #tiles div h3{
        font-size: 2rem;
    }
    
    body #statistics #tiles div p{
        font-size: 1.3rem;
    }

    body .img-wrapper{
        width: 7rem;
        height: 7rem;
    }

    #statistics #tiles div img{
        width: 4rem;
        height: 4rem;
    }
    
    body #statisticsHR{
        transform: translate(5rem, 18rem);
    }

    body footer #banner div{
        height: 28vh;
    }
    
    body footer #banner div h1{
        font-size: 3.5rem;
    }
    
    body footer #banner div .btnGetStarted{
        margin-top: 2rem;
    }

    body footer #footerWrapper #footer #links section{
        font-size: 2.2rem;
    }
}