#home{
    width: 100%;
    height: 70vh;
    display: flex;
    justify-content: space-between;
    margin: 2% 10%;
}

#home div{
    width: 50%;
    position: relative;
}

#home div div{
    width: 100%;
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
}

#home div div h1{
    color: #3F3D56;
    font-size: 8rem;
    font-weight: 700;
    line-height: 110%;
    margin : 2rem 0; /*new added */
}
#home div div h5{
    width: 80%;
    font-size: 1.8rem;
    font-weight: 500;
    color: #2BD1D1;
}

.btnGetStarted{
    font-size: 2rem;
    border: none;
    background: #3A3053;
    color: white;
    border-radius: 3rem;
    margin-top: 3rem;
    padding: 1.5rem 4rem;
    font-weight: 600;
}

.btnGetStarted:hover{
    transition: .3s linear;
    border-radius: 3rem;
    background: #9BE3E2;
    color: #3A3053;
    cursor: pointer;
}

#home div img{
    width: 50vw;
}







@media screen and (max-width:480px){
    #home{
        width: 100%;
        height: 80vh;
        display: flex;
        flex-direction: column;
        justify-content: space-evenly;
        align-items: center;
    }

    #home div:nth-of-type(1){
        width: 100%;
        order: 2;
        position: static; 
    }

    #home div:nth-of-type(1) div{
        width: 80%;
        position: static;
        text-align: center;
        top: auto;
        transform: none;
    }

    #home div:nth-of-type(1) div h1{
        color: #3F3D56;
        font-size: 3rem;
        font-weight: 700;
        line-height: 110%;
    }

    #home div:nth-of-type(1) div h5{
        width: 100%;
        font-size: 1.3rem;
        font-weight: 500;
        color: #2BD1D1;
    }

    body .btnGetStarted{
        font-size: 1.5rem;
    }

    #home div:nth-of-type(2){
        width: 100%;
        order: 1;
    }

    #home div img{
        width: 100%;
        height: 100%;
    }
}



@media screen and (min-width:480px) and (max-width:700px) {
    #home{
        width: 100%;
        height: 100vh;
        display: flex;
        flex-direction: column;
        justify-content: space-evenly;
        align-items: center;
    }

    #home div:nth-of-type(1){
        width: 100%;
        order: 2;
        position: static; 
    }

    #home div:nth-of-type(1) div{
        width: 80%;
        position: static;
        text-align: center;
        top: auto;
        transform: none;
    }

    #home div:nth-of-type(1) div h1{
        color: #3F3D56;
        font-size: 5rem;
        font-weight: 700;
        line-height: 110%;
    }

    #home div:nth-of-type(1) div h5{
        width: 100%;
        font-size: 1.8rem;
        font-weight: 500;
        color: #2BD1D1;
    }

    #home div:nth-of-type(2){
        width: 100%;
        order: 1;
    }

    #home div img{
        width: 100%;
        height: 50vh;
    }
}