nav{
    margin: 2% 10%;
}

nav ul{
    width: 100%;
}

nav ul #navbar{
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
}

nav ul #navbar>li{
    font-size: 2.5rem;
    font-weight: 600;
    align-self: center;
    padding: 1rem 2rem;
}

nav ul #navbar>li a{
    color: #2BD1D1;
}

nav ul #navbar>li a:hover{
    transition: .5s linear;
    color: #3F3D56;
}

.btn1{
    margin-right: 0;
    margin-left: auto;
    padding: 1rem !important;
}
.btn2{
    margin-right: 0;
    margin-left: 0;
    padding: 1rem !important;
}
.nav-button{
    border: none;
    background: white;
    border-radius: 2.5rem;
    padding: 1rem 2rem;
    font-weight: 600;
    color: #2BD1D1;
}
.nav-button:hover{
    transition: .3s linear;
    border-radius: 2.5rem;
    background: #9BE3E2;
    color: white;
    cursor: pointer;
}

nav ul #navbar #hamburger{
    width: 4rem;
    text-align: end;
    position: relative;
}

nav ul #navbar #hamburger input{
    width: 4rem;
    height: 4rem;
    opacity: 0;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%,-50%);
    z-index: 2;
}

nav ul #navbar #hamburger #lines{
    width: 100%;
    height: 4rem;
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    z-index: 1;
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    align-items: center;
}

nav ul #navbar #hamburger #lines span{
    width: 100%;
    height: .5rem;
    background: #3F3D56;
}

nav ul #navbar #hamburger #dropdown{
    /* width: 80vw; */
    width: 35rem;
    text-align: center;
    position: absolute;
    z-index: 3;
    right: 0%;
    top: -100rem;
    border-radius: 1rem;
    background: #3A3053;
    padding: 1rem 0;
    transition: .3s ease-in-out;
}

nav ul #navbar #hamburger #dropdown>li{
    width: 80%;
    border-radius: 3rem;
    margin: 1rem auto;
}

nav ul #navbar #hamburger #dropdown>li a{
    display: block;
    font-size: 2rem;
    font-weight: 600;
    padding: 1rem 0;
    color: white;
}

nav ul #navbar #hamburger #dropdown li:hover{
    background: #2BD1D1;
}

nav ul #navbar #hamburger #dropdown>li button{
    font-size: 2rem;
    font-weight: 600;
    padding: 1rem 0;
    color: white;
    border: none;
    background: transparent;
}

nav ul #navbar #hamburger #dropdown hr{
    width: 90%;
    height: .1rem;
    border: none;
    background: #28D2D1;
    transform: none;
    margin: 0 auto;
}

nav ul #navbar #hamburger #lines #line1{
    transition: .3s linear;
    transform-origin: 30% 0%;
}
nav ul #navbar #hamburger #lines #line2{
    transition: .3s linear;
}
nav ul #navbar #hamburger #lines #line3{
    transition: .3s linear;
    transform-origin: 15% 50%;
}
nav ul #navbar #hamburger input:checked ~ #lines #line1{
    transition: .3s linear;
    transform: rotate(45deg);
}
nav ul #navbar #hamburger input:checked ~ #lines #line2{
    transition: .3s linear;
    display: none;
}
nav ul #navbar #hamburger input:checked ~ #lines #line3{
    transition: .3s linear;
    transform: rotate(-45deg);
}
nav ul #navbar #hamburger input:checked ~ #dropdown{
    transition: .3s ease-in-out;
    top: 3rem;
}




@media screen and (max-width:480px){
    nav ul #navbar #hamburger #dropdown{
        width: 80vw;
    }

    nav ul #navbar #hamburger #dropdown>li a{
        font-size: 1.2rem;
    }

    nav ul #navbar #hamburger #dropdown>li button{
        font-size: 1.2rem;
    }
}


@media screen and (min-width : 701px) {
    nav ul #navbar #hamburger{
        display: none;
    }
}
@media screen and (max-width : 700px) {
    nav ul #navbar > li:not(#logo){
        display: none;
    }
}