#statistics{
    /* width: 100%; */
    padding: 2% 10%;
    background: #F0F1F7;
    padding-bottom: 15%;
}

#statistics #title{
    width: 60%;
    text-align: center;
    margin: 10% auto;
}

#statistics #title h1{
    color: #3F3D56;
    font-size: 5rem;
    font-weight: 700;
    line-height: 110%;
    margin-bottom: 2rem;
}

#statistics #title h5{
    font-size: 2rem;
    font-weight: 500;
    color: #2BD1D1;
}

#statistics #tiles{
    width: 100%;
    padding: 2rem;
    display: flex;
    align-items: center;
    justify-content: space-between;
}

#statistics #tiles>div{
    width: 31%;
    padding: 0 3rem 3rem 3rem;
    background: white;
}

#statistics #tiles div h3{
    font-size: 2.5rem;
    font-weight: 700;
    margin-bottom: 2rem;
}

#statistics #tiles div p{
    font-size: 1.5rem;
    line-height: 170%;
}

#statistics #tiles #tile-recognition{
    transform: translateY(0%);
}
#statistics #tiles #tile-records{
    transform: translateY(15%);
}
#statistics #tiles #tile-customizable{
    transform: translateY(30%);
}

.img-wrapper{
    width: 9rem;
    height: 9rem;
    background: #3A3053;
    border-radius: 50%;
    top: -4rem;
    display: flex;
    justify-content: center;
    align-items: center;
    position: relative;
}

#statistics #tiles div img{
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
}

#statisticsHR{
    width: 80%;
    height: 1rem;
    background: #28D2D1;
    transform: translate(5rem, 20rem);
}











@media screen and (max-width:480px) {

    #statistics #title{
        width:100%;
        margin: 20% auto;
    }
    
    #statistics #title h1{
        font-size: 2rem;
    }
    
    #statistics #title h5{
        font-size: 1.2rem;
    }
    
    #statistics #tiles{
        width: 100%;
        flex-direction: column;
        justify-content: space-between;
        align-items: center;
    }
    
    #statistics #tiles>div{
        text-align: center;
        width: 100%;
        margin-bottom: 10rem;
    }

    #statistics #tiles>div div{
        margin: 0 auto;
    }
    
    #statistics #tiles div h3{
        font-size: 1.5rem;
    }
    
    #statistics #tiles div p{
        font-size: 1rem;
        line-height: 200%;
    }
    
    #statistics #tiles #tile-recognition{
        transform: none;
    }
    #statistics #tiles #tile-records{
        transform: none;
    }
    #statistics #tiles #tile-customizable{
        transform: none;
    }
    
    .img-wrapper{
        width: 5rem;
        height: 5rem;
        background: #3A3053;
        border-radius: 50%;
        top: -2.5rem;
    }

    #statistics #tiles div img{
        width: 3rem;
        height: 3rem;
    }
    
    #statisticsHR{
        display: none;
    }    
}




@media screen and (min-width:481px) and (max-width:700px) {
        
    #statistics #title{
        width:100%;
        margin: 20% auto;
    }
    
    #statistics #title h1{
        font-size: 3rem;
    }
    
    #statistics #title h5{
        font-size: 1.5rem;
    }
    
    #statistics #tiles{
        width: 100%;
        flex-direction: column;
        justify-content: space-between;
        align-items: center;
    }
    
    #statistics #tiles>div{
        text-align: center;
        width: 80%;
        margin-bottom: 10rem;
    }

    #statistics #tiles>div div{
        margin: 0 auto;
    }
    
    #statistics #tiles div h3{
        font-size: 2rem;
    }
    
    #statistics #tiles div p{
        font-size: 1.5rem;
        line-height: 200%;
    }
    
    #statistics #tiles #tile-recognition{
        transform: none;
    }
    #statistics #tiles #tile-records{
        transform: none;
    }
    #statistics #tiles #tile-customizable{
        transform: none;
    }
    
    .img-wrapper{
        width: 7rem;
        height: 7rem;
        background: #3A3053;
        border-radius: 50%;
    }
    
    #statisticsHR{
        display: none;
    }    
}